import { Component, OnInit, Input, Output, EventEmitter, OnChanges } from '@angular/core';
import { CusOperationHttpService } from '../../../../../http/custom-operation/custom-operation.http.service';
import { CustomBatchUpdetailBean } from './bean/custom-batch-updetail.bean'
import { Message } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';

@Component({
  selector: 'app-custom-batch-updetail',
  templateUrl: './custom-batch-updetail.component.html',
  styleUrls: ['./custom-batch-updetail.component.scss'],
  providers: [CusOperationHttpService]
})
export class CustomBatchUpdetailComponent implements OnChanges {
  @Input() inValue: any;
  @Output() outValue: EventEmitter<any> = new EventEmitter();
  customBatchUpdetailBean: CustomBatchUpdetailBean = new CustomBatchUpdetailBean();
  // 表格中显示的信息
  tabMesg: any = tableMessage;
  //接收后台返回数据
  tableData: any;
  //接收后台返回的总条数
  total: any;
  //提示信息
  msgs: Message[] = [];
  //接收父级带过来的fileName和fileUrl
  customBatchUpdetail: any = [];
  //
  fileMessageList: any
  constructor(
    public cusOperationHttpService: CusOperationHttpService
  ) { }

  ngOnChanges() {
    this.customBatchUpdetailBean.pageNum = 1;
    this.customBatchUpdetailBean.pageSize = 10;
    // 
    let aa = this.inValue[0];
    this.customBatchUpdetailBean.fileName = aa.fileName;
    this.customBatchUpdetailBean.fileUrl = aa.fileUrl;
    this.query()
  }
  fuzhi() {
    this.customBatchUpdetail = this.inValue;
  }
  //页面初始查询
  query() {
    this.cusOperationHttpService.applyCustBatchQry(this.customBatchUpdetailBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tableData = data.batchFileImportInfo
        this.total = data.totalNum
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //申领事件
  applyBatch(event) {

    // this.customBatchUpdetailBean.fileName = this.inValue[0].fileName
    // this.customBatchUpdetailBean.fileUrl = this.inValue[0].fileUrl
    this.cusOperationHttpService.applyCustBatch(this.customBatchUpdetailBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: '查询成功' });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //客户批量申领
  apply() {
    // let distributeRelationModifyInfo = []
    // this.checkValue.forEach((i) => {
    // 	let obj = {};
    // 	obj['custName'] = this.data[i].custName
    // 	obj['custNo'] = this.data[i].custNo
    // 	distributeRelationModifyInfo.push(obj)
    // });
    let obj = {}
    this.inValue.forEach(item => {
      obj['fileName'] = this.inValue[0].fileName
      obj['fileUrl'] = this.inValue[0].fileUrl
    })
    this.cusOperationHttpService.applyCustBatch(obj).subscribe(data => {
      if (data.returnCode.code == 'success') {
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: '申领成功' });
        this.outValue.emit(false);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //分页
  paginate(event) {
    //每页显示的条数
    this.customBatchUpdetailBean.pageSize = event.rows;
    //当前页
    let currentPage = event.page + 1;
    this.customBatchUpdetailBean.pageNum = currentPage;
    //调用查询的方法
    this.query();
    this.customBatchUpdetailBean.pageNum = 1;
  }
  //取消
  cancel() {
    this.outValue.emit(false);
  }
}
