import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { CustomWaitApply } from "./bean/customWaitApply.bean";
import { Message, } from 'primeng/components/common/api';
// import { IsHoldCrdCard } from "app/pages/tzb/custom/constant/custOperation.codeValue";
import { Router } from '@angular/router';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-custom-wait-apply',
  templateUrl: './custom-wait-apply.component.html',
  styleUrls: ['./custom-wait-apply.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class CustomWaitApplyComponent implements OnInit {

  tabMesg: any = tableMessage;
  first: number = 0;
  customWaitApply: CustomWaitApply = new CustomWaitApply();
  msgs: Message[] = [];
  tablelist: any = [];
  display: boolean;
  totalNum: number = 0;
  // isHoldCrdCard: any[] = IsHoldCrdCard;
  isHoldCrdCard: any = [];
  paramData: any;
  mesg: any = {};
  //获取所有码值
  code: any;

  constructor(
    public httpService: CusOperationHttpService,
    public router: Router,
    public commfunc: Commfunc,
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.isHoldCrdCard = this.code['IsHoldCrdCard'];
  }

  ngOnInit() {
    this.customWaitApply.pageSize = 10;
    this.customWaitApply.pageNum = 1;
    this.querySecle();
  }

  //初始化
  querySecle() {
    this.customWaitApply.pageSize = 10;
    this.customWaitApply.pageNum = 1;
    this.first = 0;
    this.doQuery();
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  //点击查询
  doQuery() {

    this.customWaitApply.operationType = '2';//操作类型‘申领’
    this.customWaitApply.taskCategoryId = '293000';//任务id
    this.customWaitApply.isAppr = '0';//未审批

    this.httpService.queryWaitApprApplyFor(this.customWaitApply).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tablelist = data.resultList;
        this.totalNum = data.total;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //列表分页
  paginate(event) {

    this.customWaitApply.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.customWaitApply.pageNum = currentPage;
    this.first = (currentPage - 1) * this.customWaitApply.pageSize;
    this.doQuery();
  }

  //点击审批
  doExam(dataParam) {

    this.mesg['scheduleId'] = dataParam.scheduleId;
    this.mesg['taskCategoryId'] = dataParam.taskCategoryId;
    this.commfunc.setSessionData('para', JSON.stringify(this.mesg));
    let param = { scheduleId: dataParam.scheduleId, taskCategoryId: dataParam.taskCategoryId }
    let ob = this.httpService.qryScheduleByCondition(param);
    ob.subscribe(data => {
      this.paramData = {
        scheduleId: data.scheduleId,
        createDate: data.createDate,
        creater: data.creater,
        createrName: data.createrName,
        scheduleName: data.scheduleName,
        scheduleTypeId: data.scheduleTypeId,
        statusId: dataParam.statusId,
        subSysId: data.subSysId,
      }
      this.router.navigate(['/pages/tzb/uisftech-view/check/marking/a/b/c/' + dataParam.operationType + "/" + dataParam.approveNo + "/" + dataParam.batchNo + "/" + dataParam.scheduleId + "/" + dataParam.taskCategoryId, this.paramData]);
    })
  }

  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
  compileStr(cust){
		return this.commfunc.compileStr(cust)
	  }
}
