import { Routes, RouterModule } from '@angular/router';
import { ModuleWithProviders } from '@angular/core';
import { CustomApply } from './custom-apply.component';//客户申领
import { CustomMindApply } from './custom-manager/custom-mind-apply/custom-mind-apply.component';//客户申领(有主)
import { CustomDerelictionApply } from './custom-manager/custom-dereliction-apply/custom-dereliction-apply.component';//客户申领(无主)
import { CustomBatchApply } from './custom-manager/custom-batch-apply/custom-batch-apply.component';//客户批量审批
import { MyApply } from './custom-manager/my-apply/my-apply.component';//我的申领
import { customManager } from './custom-manager/custom-manager.component';//客户经理
import { CustomDoApplyComponent } from "app/pages/tzb/custom/custom-marketing/custom-apply/custom-manager/custom-do-apply/custom-do-apply.component";
import { CustomWaitApplyComponent } from "app/pages/tzb/custom/custom-marketing/custom-apply/custom-manager/custom-wait-apply/custom-wait-apply.component";
import { CustomAlreadyApplyComponent } from "app/pages/tzb/custom/custom-marketing/custom-apply/custom-manager/custom-already-apply/custom-already-apply.component";
import { SubranchManager } from './subranch-manager/subranch-manager.component';//我要申领中的支行行长
import { PendingApply } from './subranch-manager/pending-apply/pending-apply.component';//待审批申领
/**
 * 客户申领
 * 
 *
 */
export const routes: Routes = [

    {
        path: '',
        component: CustomApply,
        children: [
            {
                path: 'custom-manager',
                component: customManager,
                children: [
                    { path: '', redirectTo: 'custom-do-apply', pathMatch: 'full' },
                    { path: 'custom-do-apply', component: CustomDoApplyComponent },
                    { path: 'custom-mind-apply', component: CustomMindApply },
                    { path: 'custom-dereliction-apply', component: CustomDerelictionApply },
                    { path: 'custom-batch-apply', component: CustomBatchApply },
                    { path: 'my-apply', component: MyApply },
                    { path: 'custom-wait-apply', component: CustomWaitApplyComponent },
                    { path: 'custom-already-apply', component: CustomAlreadyApplyComponent },
                ]
            },
            {
                path: 'subranch-manager',
                component: SubranchManager,
                children: [
                    { path: '', redirectTo: 'pending-apply', pathMatch: 'full' },
                    { path: 'pending-apply', component: PendingApply },
                ]
            }
        ]
    }

]

export const CusApplyRouting: ModuleWithProviders = RouterModule.forChild(routes);
