import { Component, Input, OnInit, Output, OnChanges, EventEmitter } from '@angular/core';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Message } from 'primeng/components/common/api';
// import { IsStatusApply } from "app/pages/tzb/custom/constant/custOperation.codeValue";
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-apply-detail',
  templateUrl: './apply-detail.component.html',
  styleUrls: ['./apply-detail.component.scss'],
  providers: [CusOperationHttpService, Commfunc]
})
export class ApplyDetailComponent implements OnChanges {

  @Output() outValue = new EventEmitter();
  @Input('in-value') inValue: any;
  tabMesg: any = tableMessage;
  tablelist: any = [];
  totalNum: number = 0;
  msgs: Message[] = [];
  param: any;
  isStatus: any = [];
  chooseData: any = [];
  //获取所有码值
  code: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
  ) {

    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.isStatus = this.code['IsStatusApply'];
  }

  ngOnChanges() {
    if (this.inValue) {
      this.param = { businessId: this.inValue };
      this.doQuery();
    }
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  doQuery() {
    this.httpService.qryProcessByBusId(this.param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tablelist = data.recordList;
        this.totalNum = data.totalNum;
        if (this.tablelist) {
          if (this.tablelist.length > 0) {
            this.tablelist.forEach((element, index) => {
              if (element.statusId == null) {
                this.tablelist[index].statusId = '00';
              }
            });
          }
        }
        this.chooseData = this.tablelist;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  //关闭
  close() {
    this.outValue.emit(false);
  }

}
