import { WorkdeskApllyBean } from "app/pages/tzb/custom/custom-marketing/custom-apply/custom-manager/workdesk-apply/bean/workdesk-apply.bean";
import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { Message, ConfirmationService } from 'primeng/primeng';


@Component({
  selector: 'app-workdesk-apply',
  templateUrl: './workdesk-apply.component.html',
  styleUrls: ['./workdesk-apply.component.scss']
})
export class WorkdeskApplyComponent implements OnInit {

  @Output() outValue = new EventEmitter();
  @Input('in-value') inValues: any;
  @Output() outValueBean: EventEmitter<any> = new EventEmitter();

  msgs: Message[] = [];
  workdeskApllyBean: WorkdeskApllyBean = new WorkdeskApllyBean();
  headerTitle: string;
  display: boolean;
  peopleList: any;
  user: any;
  user1: any;
  userName: string = '';
  orgName: string = '';
  userId: string = '';
  orgId: string = '';
  receiverName: string = '';
  dates: any = [];
  waitSelUserList: any;
  outVal: any;
  aaaa: boolean = false;
  iskzh: string = '';
  iszlxr: string = '';
  dfOrg: string = '';

  constructor(
    public commfunc: Commfunc,
    public router: Router,
    public httpService: CusOperationHttpService,
  ) {
    this.user1 = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.user = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    if (this.user1.userId) {
      this.userId = this.user1.userId;
    }
    if (this.user1.orgId) {
      this.orgId = this.user1.orgId;
    }
    if (this.user.userName) {
      this.userName = this.user.userName;
    }
    if (this.user.orgName) {
      this.orgName = this.user.orgName;
    }
    this.workdeskApllyBean.creater = this.userId;
    this.workdeskApllyBean.createrName = this.userName;
    this.workdeskApllyBean.createOrgId = this.orgId;
    this.workdeskApllyBean.createOrgName = this.orgName;
    this.workdeskApllyBean.receiveOrgName = this.workdeskApllyBean.createOrgName;
    this.workdeskApllyBean.receiveOrgId = this.workdeskApllyBean.createOrgId;
    this.workdeskApllyBean.receiverName = this.workdeskApllyBean.createrName;
    this.workdeskApllyBean.receiver = this.workdeskApllyBean.creater;
  }

  ngOnInit() {

  }

  //确定
  confirmBtn() {
    if (this.inValues.type == '1') {
      this.workdeskApllyBean.operationType = '1';//分配
    }
    if (this.inValues.type == '2') {
      this.workdeskApllyBean.operationType = '2';//分配
    }
    if (this.inValues.type == '3') {
      this.workdeskApllyBean.operationType = '3';//分配
    }
    this.dates = []
    if (this.inValues.invalue[0]) {
      this.dates = this.inValues.invalue;
    } else {
      this.dates.push(this.inValues.invalue);
    }
    this.workdeskApllyBean.custList = this.dates;
    this.outValueBean.emit(this.workdeskApllyBean);

    this.httpService.checkInterSubBranch(this.workdeskApllyBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.iskzh = data.iskzh;//是否跨支行
        this.iszlxr = data.iszlxr;//是否有主联系人
        this.dfOrg = data.dfOrg;//是否有主联系人
        this.waitSelUserList = this.commfunc.query('293000', {iskzh:this.iskzh,iszlxr: this.iszlxr,dfOrg:this.dfOrg});
        this.outVal = this.waitSelUserList.waitSelUserList;
        this.aaaa = this.waitSelUserList.display;
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        let a = commonHeader.extParam.specifyNextOpers;
        if (this.aaaa == true && (a == undefined || a == '' || a == null)) {
          this.outValue.emit({ outVal: this.outVal, aaaa: this.aaaa });
        } else {
          this.doSubmit();
        }
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })


  }

  doSubmit() {
    this.httpService.insertCustManageData(this.workdeskApllyBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
        let a9 = data2 => { this.outValue.emit(false) }
        setTimeout(a9, 1000);
      } else {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      if (s.extParam.specifyNextOpers) {
        delete s.extParam.specifyNextOpers
        let commonHeader = s;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
      }
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //取消
  cancelBtn() {
    this.outValue.emit(false);
  }

  //机构树
  orgTree() {
    this.headerTitle = '机构树';
    this.display = true;
  }

  //机构树回调
  treeCall(param) {
    this.display = false;
    this.workdeskApllyBean.receiveOrgName = param.orgName;
    this.workdeskApllyBean.receiveOrgId = param.orgId;
  }
}
