import { Component, OnInit } from '@angular/core';
import { CusttomDoApplyBean } from "./bean/custtomDoApply.bean";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { ConfirmDialogModule, ConfirmationService } from 'primeng/primeng';
import { Message } from 'primeng/components/common/api';
// import { CERT_TYPE } from "app/pages/tzb/custom/constant/custOperation.codeValue";

@Component({
  selector: 'app-custom-do-apply',
  templateUrl: './custom-do-apply.component.html',
  styleUrls: ['./custom-do-apply.component.scss'],
  providers: [CusOperationHttpService, Commfunc, ConfirmationService]
})
export class CustomDoApplyComponent implements OnInit {

  custtomDoApplyBean: CusttomDoApplyBean = new CusttomDoApplyBean();
  first: number = 0;//分页控制
  totalNum: number = 0;
  msgs: Message[] = [];
  tablelist: any = [];
  cert_type: any = [];
  chooseData: any = [];
  inValue: any;
  display: boolean = false;
  outVal: any;
  aaaa: boolean = false;
  outValBean: any;
  //获取所有码值
  code: any;

  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
  ) {
    this.codeValues() //调用方法，获取全部码值
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
		this.cert_type = this.code['CERT_TYPE'];
  }

  ngOnInit() {
    this.custtomDoApplyBean.pageSize = 10;
    this.custtomDoApplyBean.pageNum = 1;
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  //查询初始化
  querySecle() {
    this.custtomDoApplyBean.pageSize = 10;
    this.custtomDoApplyBean.pageNum = 1;
    this.first = 0;
    this.doQuery();
  }

  //查询
  doQuery() {
    // 是否输入客户号
    if (
      this.custtomDoApplyBean.custNo == undefined ||
      this.custtomDoApplyBean.custNo == ''
    ) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '请输入客户号' });
      return
    }
    // 只允许输入数字
    var reg = /^[0-9]*$/;
    if (this.custtomDoApplyBean.custNo != undefined) {
      if (this.custtomDoApplyBean.custNo != '') {
        if (!reg.test(this.custtomDoApplyBean.custNo)) {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '提示', detail: '只允许输入数字' });
          return
        }
      }
    }
    this.custtomDoApplyBean.operationType = '2';
    this.httpService.distributeCustQry(this.custtomDoApplyBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.tablelist = data.custList;
        if (this.tablelist.length > 0) {
          if (this.tablelist[0].ownerManagerName == '' || this.tablelist[0].ownerManagerName == null) {
            this.tablelist[0].ownerManagerName = '-';
          }
          this.chooseData = this.tablelist[0];
        }else{
          this.chooseData=[];
        }
        this.totalNum = data.totalCount;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: data.returnCode.message });
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: "调用服务失败！" });
    })
  }

  //分页
  paginate(event) {
    this.custtomDoApplyBean.pageSize = event.rows * 1;
    let currentPage = event.page + 1;
    this.custtomDoApplyBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.custtomDoApplyBean.pageSize;
    this.doQuery();
  }

  //分配
  doAllot(param) {
    if (param == 'more') {
      if (this.chooseData.length == 0) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: '请选择客户！' });
        return
      } else {

        this.inValue = {
          invalue: this.chooseData,
          type: '2'
        }
        this.display = true;
      }
    }
    else {

      this.inValue = {
        invalue: param,
        type: '2'
      }
      this.display = true;
    }

  }

  //关闭回调
  doMoreAllotCall(param) {

    if (param == false) {
      this.tablelist = [];
      this.chooseData = [];
      this.custtomDoApplyBean.custNo = undefined;
      this.display = false;
    } else {
      this.display = false;
      this.outVal = param.outVal;
      this.aaaa = param.aaaa;
    }
  }

  onHideClose() {
    document.body.style.overflow = 'auto';
    this.tablelist = [];
    this.chooseData = [];
    this.custtomDoApplyBean.custNo = undefined;
    this.display = false;
  }

  outDisplayCall(event) {

    this.aaaa = event;
    this.display = false;
  }

  outValueCall(event) {

    this.outVal = event;
    let ss = '';
    this.outVal.forEach(item => {
      ss += item.tellerId + ','
    });
    let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    commonHeader.extParam['specifyNextOpers'] = ss;
    this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));

    this.httpService.insertCustManageData(this.outValBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.aaaa = false;
        this.display = false;
        // this.msgs = [];
        // this.msgs.push({ severity: 'success', summary: '提示', detail: '调用服务成功！' });
      } else {
        let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        if (s.extParam.specifyNextOpers) {
          delete s.extParam.specifyNextOpers
          let commonHeader = s;
          this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
        }
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
      if (s.extParam.specifyNextOpers) {
        delete s.extParam.specifyNextOpers
        let commonHeader = s;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
      }
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    });
  }

  addCallBean(event) {
    this.outValBean = event;
    this.tablelist = [];
  }

  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

  compileStr(cust){
		return this.commfunc.compileStr(cust)
	  }

}
