import { Component, OnInit } from '@angular/core';
import { CusOperationHttpService } from "app/pages/tzb/custom/http/custom-operation/custom-operation.http.service";
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router } from '@angular/router'

@Component({
	selector: 'custom-manager',
	templateUrl: './custom-manager.component.html',
	styleUrls: ['./custom-manager.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class customManager implements OnInit {

	totalNum: number = 0;
	flag: boolean = false;
	show:number;

	constructor(
		public httpService: CusOperationHttpService,
		public commfunc: Commfunc,
		public router: Router,
	) {
	}

	//初始化
	ngOnInit() {
		
		let stu_1 = this.commfunc.permissionCheck('SID01003_P005');
		let stu_2 = this.commfunc.permissionCheck('SID01003_P006');
		let stu_3 = this.commfunc.permissionCheck('SID01003_P007');
		let stu_4 = this.commfunc.permissionCheck('SID01003_P008');

		if(stu_1){
			this.show = 1;
			this.router.navigate( ['/pages/tzb/custom/custom-marketing/cus-apply/custom-manager/custom-do-apply'])
		}else if(stu_2){
			this.show = 2;
			this.router.navigate( ['/pages/tzb/custom/custom-marketing/cus-apply/custom-manager/my-apply'])
		}else if(stu_3){
			this.show = 3;
			this.router.navigate( ['/pages/tzb/custom/custom-marketing/cus-apply/custom-manager/custom-wait-apply'])
		}else if(stu_4){
			this.show = 4;
			this.router.navigate( ['/pages/tzb/custom/custom-marketing/cus-apply/custom-manager/custom-already-apply'])
		}
		this.doQuery();
	}

	//切换按钮
	apply(num) {
		this.show = num;
	}

	//点击查询
	doQuery() {

		let param = {
			pageSize: 10,
			pageNum: 1,
			operationType: '2',
			taskCategoryId: '293000',
			isAppr: '0',
		}

		this.httpService.queryWaitApprApplyFor(param).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.totalNum = data.total;
			} else {
			}
		}, error => {
		})
	}

	//按钮权限
	permissionCheck(id) {
		return this.commfunc.permissionCheck(id)
	}
}
