/**
 * 客户申领有主
 */
import { Component, OnInit } from '@angular/core';
import { ApplyCustApplyedSearchBean } from './bean/applyCustApplyedSearch.bean';//我要申领客户查询（有主）
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
// import { IsAllot, ProdHoldLoan, Gender } from '../../../../constant/custOperation.codeValue'  //引入code码是否分配管护人  当前是否持有贷款 当前是否持有信用卡 
import { Message } from 'primeng/primeng';
import { CALENDAR_CN, tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import { Router } from '@angular/router';
import { CommonHttpService } from '../../../../../common/http/common.http.service';


@Component({
	selector: 'custom-mind-apply',
	templateUrl: './custom-mind-apply.component.html',
	styleUrls: ['./custom-mind-apply.component.scss'],
	providers: [CusOperationHttpService, Commfunc]
})
export class CustomMindApply implements OnInit {
	// 表格中显示的信息
	tabMesg: any = tableMessage;
	startDate: string;
	endDate: string;
	calendar: any = [];
	customerQuery: string = "客户查询条件";
	customerSearch: string = "客户搜索条件";
	data: any;						//后台接收的数据
	distributeCustQryForm: FormGroup;
	prodHoldLoan: any = [];
	isCurrentHoldCreditcard: any = [];//是都有信用卡管道
	isCurrentHoldLoan: any = [];//是都有贷款管道
	custNo: any = [];				//客户号
	gender: any = [];			//管道转性别

	total: number;					//总页数

	pageSize: number = 10;	   		//每页多少条
	checkValue: any = [];
	deletNum: any;
	ch: any = CALENDAR_CN;
	temData: Date = new Date();
	applyCustApplyedSearchBean: ApplyCustApplyedSearchBean = new ApplyCustApplyedSearchBean();
	isAllot: any = [];		//是否分配管护人  当前是否持有贷款 当前是否持有信用卡 
	managerRelationStartDate: any;
	managerRelationEndDate: any;
	headerTitle: string;			//模态框的标题
	display: boolean;				//控制模态框显示隐藏
	showModel: string;
	treeOrgName: string;
	empOrgId: string;				//接收机构树上的需要数据
	orgId: string;					//接收需要的数据
	peopleList: any;				//接收返回柜员信息
	msgs: Message[] = [];			//提示信息
	userform: FormGroup; 			//校验	
	//获取所有码值
	code: any;

	constructor(
		public httpService: CusOperationHttpService,
		public commonHttpService: CommonHttpService,
		public fb: FormBuilder,
		public commfunc: Commfunc,
		public router: Router
	) {
		this.userform = fb.group({
			custName: ['', [Validators.pattern(/^[\u4E00-\u9FA5]{1,20}$/)]],//客户姓名
			custNo: ['', [Validators.pattern(/^[\w]{1,20}$/)]],//客户号
			idenNum: ['', [Validators.pattern(/^[\w]/)]],//证件号
			'depositAverageTheYearStart': new FormControl('', [Validators.pattern(/^[0-9]*$/)]),//存款日均
			'depositAverageTheYearEnd': new FormControl('', [Validators.pattern(/^[0-9]*$/)]),//存款日均
			'financingTheYearStart': new FormControl('', [Validators.pattern(/^[0-9]*$/)]),//理财日均
			'financingTheYearEnd': new FormControl('', [Validators.pattern(/^[0-9]*$/)]),//理财日均
		})

		this.codeValues() //调用方法，获取全部码值
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
		this.prodHoldLoan = this.code['ProdHoldLoan'];
		this.isCurrentHoldCreditcard = this.code['IsAllot'];
		this.isCurrentHoldLoan = this.code['IsAllot'];
		this.gender = this.code['Gender'];
		this.isAllot = this.code['IsAllot'];
	}

	ngOnInit() {
		this.applyCustApplyedSearchBean.pageSize = 10;
		this.applyCustApplyedSearchBean.pageNum = 1;
		// this.query();
	}

	//码值
	codeValues() {
		this.commfunc.codeValue();
		let ret = this.commfunc.codeDatas;
		return this.code = ret //code是自己声明的变量
	}

	//日期
	showData() {
		this.temData = this.managerRelationStartDate;
	}
	//根据机构号查询机构人员queryTellerByCombConditions
	queryPeopleById() {
		// 
		if (this.applyCustApplyedSearchBean.empOrgId) {
			let param = {
				orgId: this.applyCustApplyedSearchBean.empOrgId,
				pageSize: 1000000
			}
			this.commonHttpService.queryTellerByCombConditions(param).subscribe(data => {
				if (data.tellerList) {
					this.peopleList = this.commfunc.listToLabelvalue(data.tellerList, 'tellerName', 'tellerId');
					this.applyCustApplyedSearchBean.empName = this.peopleList[0].tellerName;
					this.applyCustApplyedSearchBean.empNo = this.peopleList[0].tellerId;
				}
			})
		}
	}
	// 初始查询
	query() {
		let param = {}
		this.httpService.applyCustApplyedSearch(param).subscribe(data => {
			if (data.returnCode.code = 'success') {
				this.data = data.distributeCust;
				this.total = data.totalNum;
				// this.msgs = [];
				// this.msgs.push({ severity: 'success', summary: '提示', detail: '查询成功' });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}

		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}
	/**
  * 我要申领（有主）查询按钮
  */
	check() {
		this.applyCustApplyedSearchBean.managerRelationStartDate = this.commfunc.transDateN(this.managerRelationStartDate);
		this.applyCustApplyedSearchBean.managerRelationEndDate = this.commfunc.transDateN(this.managerRelationEndDate);
		this.applyCustApplyedSearchBean.empOrgId = this.orgId
		let ob = this.httpService.applyCustApplyedSearch(this.applyCustApplyedSearchBean);
		ob.subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.data = data.distributeCust;
				this.total = data.totalNum;
				// this.msgs = [];
				// this.msgs.push({ severity: 'success', summary: '提示', detail: '查询成功' });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})
	}
	//重置按钮
	again() {
		this.applyCustApplyedSearchBean = new ApplyCustApplyedSearchBean();
	}
	/**
	 * 我要申领(有主)申领按钮
	 */
	apply() {
		let distributeRelationModifyInfo = []
		this.checkValue.forEach((i) => {
			let obj = {};
			obj['custName'] = this.data[i].custName
			obj['custNo'] = this.data[i].custNo
			distributeRelationModifyInfo.push(obj)
		});

		let ob = this.httpService.applyCustApplyed({ distributeRelationModifyInfo: distributeRelationModifyInfo }).subscribe(data => {
			if (data.returnCode.code == 'success') {
				this.data = data.distributeRelationModifyInfo;
				this.query()
				this.checkValue = '';
				// this.msgs = [];
				// this.msgs.push({ severity: 'success', summary: '提示', detail: '申领成功' });
			} else {
				this.msgs = [];
				this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
			}
		}, error => {
			this.msgs = [];
			this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
		})

	}
	// deleBtn(car){
	// 	this.deletNum = car;
	// }
	//分页
	paginate(event) {
		//每页显示的条数
		this.applyCustApplyedSearchBean.pageSize = event.rows * 1;
		//当前页
		let currentPage = event.page + 1;
		this.applyCustApplyedSearchBean.pageNum = currentPage;
		//调用查询的方法

		this.check();
		this.applyCustApplyedSearchBean.pageNum = 1;
		this.checkValue = '';

	}
	//机构树
	orgTree() {
		this.headerTitle = '机构树';
		this.display = true;
		this.showModel = '6';
	}
	treeCall(param) {
		this.display = false;
		this.applyCustApplyedSearchBean.empOrgName = param.orgName;
		this.applyCustApplyedSearchBean.empOrgId = param.orgId;
		this.queryPeopleById();
	}
}
