import { Component, OnInit } from '@angular/core';
import { TZB_HTTP_CUS } from 'app/@uisftech/common/service/http.constant';
import { TZB_CUS_UPLOAD, TZB_CUS_DOWNLOAD } from 'app/@uisftech/common/service/http.constant';
import { CusOperationHttpService } from '../../../../http/custom-operation/custom-operation.http.service';
import { Message } from 'primeng/primeng';
import { Commfunc } from 'app/@uisftech/common/service/commonFun';

@Component({
  selector: 'app-custom-batch-apply',
  templateUrl: './custom-batch-apply.component.html',
  styleUrls: ['./custom-batch-apply.component.scss'],
  providers: [CusOperationHttpService]

})
export class CustomBatchApply implements OnInit {
  files: any = [];
  fileList: any = [];//上传文件列表
  fileUrl: string;
  fileName: string;
  DOWNLOAD: string = TZB_CUS_DOWNLOAD;
  UPLOAD: string = TZB_CUS_UPLOAD;
  //提示信息
  msgs: Message[] = [];
  //模态框的标题
  headerTitle: string = "修改";
  //模态框是否显示
  display: boolean = false;
  showModel: string;
  //参数项
  detailValue: string;
  //获取所有码值
  code: any;

  constructor(
    public cusOperationHttpService: CusOperationHttpService,
    public commfunc: Commfunc,
  ) {
    this.codeValues() //调用方法，获取全部码值
		//获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
  }

  ngOnInit() {
   
  }

  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  load(event) {
    for (let file of event.files) {
      this.files.push(file)
    }
    if (event.xhr.response) { //上传成功 后台返回的 文件路径 和文件名
      // 
      this.headerTitle = '客户批量申领';
      this.display = true;
      this.showModel = '1';
      this.fileList = JSON.parse(event.xhr.response);
      this.detailValue = this.fileList;
    }
    // let param = { fileList: this.fileList }
    // this.cusOperationHttpService.applyCustBatch(param).subscribe(data => {

    //   if (data.returnCode.code == 'success') {
    //     this.msgs = [];
    //     this.msgs.push({ severity: 'success', summary: '提示', detail: '上传成功' });
    //   } else {
    //     this.msgs = [];
    //     this.msgs.push({ severity: 'error', summary: '提示', detail: data.errorMessage });
    //   }
    // }, error => {
    //   this.msgs = [];
    //   this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    // })
  }
  //公共参数项
  detail(item) {
    this.headerTitle = '客户批量申领';
    this.display = true;
    this.showModel = '1';
    this.detailValue = item;
    //   
  }
  detailCall(param) {
    this.display = param;
    //   this.query();
    this.msgs = [];
    this.msgs.push({ severity: 'success', summary: '提示', detail: '批量申领成功' });
  }
}
